}


/* ===公共样式=== */
.full-size {
  @extend %full-size;
}

.ib {
  @extend %ib;
}

.fix {
  @extend %fix;
}

.ellipsis {
  @extend %ellipsis;
}

.min-width {
  min-width: $min-width;
}
// 隐藏
.hidden {
  display: none !important;
}
// 移除隐藏
.over-hide {
  overflow: hidden;
}
// 内联居中
.ib.middle {
  @extend .ib;
  width: 0;
  height: 100%;
  vertical-align: middle;
}
// 绝对定位
.absolute {
  position: absolute;
  left: 0;
  top: 0;
}
// 修复iphone滚动条
.fix-iphone-scroll {
  -webkit-overflow-scrolling: touch;
}

.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.v-m {
  vertical-align: middle;
}
.v-t {
  vertical-align: top !important;
}
.fl {
  float: left;
}
.fr {
  float: right;
}


// 字体尺寸
.f-12 {
  font-size: 1em;
}
.f-13 {
  font-size: 1.1em;
}
.f-14 {
  font-size: 1.2em;
}
.f-18 {
  font-size: 1.7em;
}


// 高度尺寸
.h-p-100 {
  height: 100%;
}